import React from "react";
import {Button} from "@material-ui/core";
import {connect} from "react-redux";
import {openRightPaper} from "../../../../redux/reducers/rightPaperSlice";


function ActionButton({buttonLabel, anchor, width, caseId, openRightPaper, style}) {

    const onClick = () => {
        openRightPaper({caseId, width, anchor})
    }

    return <Button onClick={onClick} color="primary" variant="outlined" style={style}>
        {buttonLabel}
    </Button>
}

const mapProps = (state) => {
    return {}
}
export default connect(
    mapProps,{
        openRightPaper,
    }
)(ActionButton)