import React from "react";
import theme from "../../../themes/inspector_light_theme";
import {ObjectInspector} from "react-inspector";
import "../lists/cards/SourceCard.css";
import DetailBox from "./DetailBox";
import {MiniHeader} from "../Headers";
import DetailHeader from "./DetailHeader";
import "./Details.css";
import "./RuleDetails.css";
import {Button} from "@material-ui/core";
import ConfirmationDialog from "../misc/ConfirmationDialog";
import MetaSwitches from "../misc/MetaSwitches";
import Properties from "./DetailProperties";

function RuleDetails({data, onDelete}) {

    const [openConfirmation, setOpenConfirmation] = React.useState(false);

    const UqlDetails = ({data}) => {
        if (typeof data.uql !== "undefined") {
            return <React.Fragment>

                <DetailBox>
                    <MiniHeader>WHEN</MiniHeader>
                    {data.uql.condition}
                </DetailBox>

                <DetailBox>
                    <MiniHeader>THEN</MiniHeader>
                    {data.uql.actions}
                </DetailBox>

                <DetailBox>
                    <MiniHeader>UQL create statement</MiniHeader>
                    {data.uql.uql}
                </DetailBox>

            </React.Fragment>
        }
        return "";
    }

    const onDeleteClick = () => {
        setOpenConfirmation(true);
    }

    const onConfirmedDelete = () => {
        setOpenConfirmation(false);
        onDelete(data.unomi.metadata.id)
    }

    const onDeleteClose = () => {
        setOpenConfirmation(false);
    }

    return <div style={{height: "inherit"}}>
        <DetailHeader label={data.unomi.metadata.name}/>

        <div className="RightTabScroller" style={{height: "calc(100% - 161px)"}}>

            {data.unomi.metadata.description && <DetailBox>
                <MiniHeader>description</MiniHeader>
                {data.unomi.metadata.description}
            </DetailBox>}

            <UqlDetails data={data}/>

            <DetailBox>
                <MiniHeader>Metadata</MiniHeader>
                <Properties properties={data.unomi.metadata} show={["scope", "tags", "systemTags"]}/>
            </DetailBox>
            <DetailBox>
                <MiniHeader>Metadata switches</MiniHeader>
                <MetaSwitches index="rule" id={data.unomi.itemId} meta={data.unomi.metadata}/>
            </DetailBox>
            <DetailBox>
                <MiniHeader>Raw unomi data</MiniHeader>
                <ObjectInspector data={data} theme={theme} expandLevel={2}/>
            </DetailBox>

        </div>
        <div className="DetailActionButtons">
            <Button onClick={onDeleteClick} variant="outlined" color="primary">
                Delete
            </Button>
            <ConfirmationDialog open={openConfirmation} title="Do you want to delete this rule?"
                                content="This action can not be undone." onClose={onDeleteClose}
                                onAgree={onConfirmedDelete}
            />
        </div>
    </div>

}

export default RuleDetails;
