import {connect} from "react-redux";
import React from "react";
import SegmentDataGrid from "../elements/lists/grids/SegmentDataGrid";
import {deleteData, getData} from "../../remote_api/uql_api_endpoint";
import SegmentDetails from "../elements/details/SegmentDetails";
import ListDetailView from "../elements/ListDetailView";
import FilterListWrapper from "../elements/FilterListWrapper";
import SegmentCard from "../elements/lists/cards/SegmentCard";
import TopInfoBar from "../TopInfoBar";
import Breadcrumps from "../elements/misc/Breadcrumps";
import ActionButton from "../elements/buttons/rule/ActionButton";
import TopProgress from "../menu/TopProgress";


const Segments = () => {

    const [loading, setLoading] = React.useState(false);
    const [error, setError] = React.useState(false);
    const [ready, setReady] = React.useState(false);
    const [deleteResponse, setDeleteResponse] = React.useState(false);

    const none = () => {}

    const onDeleteOk = (data) => {
        setDeleteResponse(data);
        setReady(false);
    }
    const onClick = (id) => {
        getData('/segment/' + id, setLoading, setError, setReady)
    }

    const onDelete = (id) => {
        deleteData('/segment/' + id, none, none, onDeleteOk);
    }

    const details = () => {
        if (ready != false) {
            return <SegmentDetails data={ready.data} onDelete={onDelete}/>
        }
    }

    const grid = () => <FilterListWrapper
        endPoinyUrl="/segment/select"
        renderList={
            (data) => {
                return <SegmentDataGrid onClick={onClick} data={data}></SegmentDataGrid>
            }
        }
        filterLabel="Type condition to filter segments"
        filterKey="segmentFilterQuery"
    />

    const blocks = () => {
        return <FilterListWrapper
            endPoinyUrl="/segment/select"
            renderList={
                (data) => {
                    return data.map((row, index) => (
                        <SegmentCard key={index} data={row} onClick={onClick}/>
                    ))
                }
            }
            filterLabel="Type condition to filter segments"
            filterKey="segmentFilterQuery"
        />
    }

    return <React.Fragment>

        <TopInfoBar buttons={[<ActionButton buttonLabel="Add segment" anchor="right" width={1000} caseId="AddSegmentForm"/>]}>
            <Breadcrumps/>
        </TopInfoBar>
        <ListDetailView list={blocks} detail={details}/>

    </React.Fragment>;
}

const mapProps = (state) => {
    return {
        notification: state.notificationReducer,
        uqlQuery: state.uqlReducer,
    }
}
export default connect(
    mapProps,
)(Segments)
