import React, {useEffect} from 'react';
import {request} from "../../remote_api/uql_api_endpoint";
import Input from "./forms/inputs/Input";
import {connect, useDispatch} from "react-redux";
import {showAlert} from "../../redux/reducers/alertSlice";
import "./FilterListWrapper.css";

export function FilterListWrapper({filterLabel, filterKey, endPoinyUrl, renderList, forceReload}) {

    const [loading, setLoading] = React.useState(false);
    const [error, setError] = React.useState(false);
    const [ready, setReady] = React.useState(false);
    const [searchQuery, setSearchQuery] = React.useState(localStorage.getItem(filterKey));
    const [inputError, setInputError] = React.useState(false);
    const [rerender, setReRender] = React.useState(0);
    const dispatch = useDispatch();

    const fetchData = (withLoading) => {

        return () => {

            let load = () => { }

            if (withLoading) {
                load = (value) => {
                    setLoading(value);
                    setInputError(false)
                }
            }

            request(
                {
                    url: endPoinyUrl,
                    method: "post",
                    header: {"Content-type": "text/plain"},
                    data: searchQuery
                },
                load,
                (value) => {
                    setError(value);
                    setInputError(true)
                },
                (value) => {
                    setReady(value);
                    setInputError(false)
                },
                withLoading
            );
        }

    }

    useEffect(() => {
        fetchData(true)();

        const interval = setInterval(fetchData(false), 5000);
        return () => clearInterval(interval);

    }, [searchQuery, rerender, forceReload])

    const onSearch = (value) => {
        localStorage.setItem(filterKey, value);
        setSearchQuery(value);
        setReRender(Math.random());
    }

    const render = () => {
        if (error !== false) {
            dispatch(showAlert({type: "warning", message: error, hideAfter: 6000}));
            setError(false);
        }
        if (ready !== false) {
            return renderList(ready.data)

        }
        // else if (loading) {
        //     return <CenteredCircularProgress/>
        // }
    }

    return (

        <div className="FilterListWrapper">
            <div className="FilterInput">
                <Input label={filterLabel}
                       onEnterPressed={onSearch}
                       initValue={searchQuery}
                       style={{width: "100%"}}
                       error={inputError}
                       variant="outlined"
                />
            </div>

            <div className="FilterList">
                {render()}
            </div>
        </div>
    );

}

const mapProps = () => {
    return {}
}

export default connect(
    mapProps,
    {
        showAlert,
    },
)(FilterListWrapper)
