import React, {useState} from "react";
import {
    BrowserRouter as Router,
    Switch,
    Route
} from "react-router-dom";
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import AppBox from "./AppBox";
import {connect, useDispatch} from "react-redux";
import {hideAlert} from "../redux/reducers/alertSlice";
import Logout from "./authentication/Logout";
import SignIn from "./authentication/SignIn";
import RightPaper from "./elements/RightPaper";
import {SwitchCase, Case} from "./elements/misc/SwitchCase";
import CreateRuleForm from "./elements/forms/CreateRuleForm";
import CreateSegmentForm from "./elements/forms/CreateSegemntForm";
import CreateSourceForm from "./elements/forms/CreateSourceForm";
import SocialSplash from "./elements/misc/SocialSplash";
import RightPaperHeader from "./elements/RightPaperHeader";
import PrivateRoute from "./authentication/PrivateRoute";

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const App = ({alert,rightPaper}) => {

    const [splash, setSplash] = useState(true);
    const dispatch = useDispatch()

    const handleClose = () => {
        dispatch(hideAlert())
    };

    const onContinueClick = () => {
        setSplash(false);
    }

    const application = () => {
        if(splash) {
            return <SocialSplash onClick={onContinueClick}/>
        } else {
            return (
                <Router>
                    <Switch>
                        <Route exact path="/app/login">
                            <SignIn/>
                        </Route>
                        <Route exact path="/app/logout">
                            <Logout/>
                        </Route>
                        <PrivateRoute path="/app" roles={["unomi"]}>
                            <AppBox/>
                        </PrivateRoute>
                    </Switch>
                    <Snackbar open={alert.show} autoHideDuration={alert.hideAfter} onClose={handleClose}>
                        <Alert onClose={handleClose} severity={alert.type}>
                            <pre style={{margin: 0, overflow: "auto", maxWidth: "500px"}}>{alert.message}</pre>
                        </Alert>
                    </Snackbar>
                    <RightPaper open={rightPaper.open}>
                        <SwitchCase caseId={rightPaper.caseId}>
                            <Case id="AddSourceForm">
                                <RightPaperHeader>
                                    <span style={{fontWeight: 700}}>Create</span>&nbsp;source
                                </RightPaperHeader>
                                <CreateSourceForm/>
                            </Case>
                            <Case id="AddRuleForm">
                                <RightPaperHeader>
                                    <span style={{fontWeight: 700}}>Create</span>&nbsp;rule
                                </RightPaperHeader>
                                <CreateRuleForm/>
                            </Case>
                            <Case id="AddSegmentForm">
                                <RightPaperHeader>
                                    <span style={{fontWeight: 700}}>Create</span>&nbsp;segment
                                </RightPaperHeader>
                                <CreateSegmentForm/>
                            </Case>
                        </SwitchCase>
                    </RightPaper>
                </Router>
            );
        }

    }

    return application();
}

const mapState = (state) => {
    return {
        alert: state.alertReducer,
        rightPaper: state.rightPaperReducer
    }
}
export default connect(mapState)(App);
