import React from "react";
import "./RightPaperHeader.css";
import {HighlightOff} from "@material-ui/icons";
import {connect} from "react-redux";
import {closeRightPaper} from "../../redux/reducers/rightPaperSlice";

function RightPaperHeader({children, closeRightPaper}) {
    return <div className="RightPaperHeader">
        <span className="Label">{children}</span>
        <HighlightOff onClick={()=>closeRightPaper()} style={{cursor: "pointer"}}/>
    </div>
};

const mapProps = (state) => {
    return {
        data: state.rightPaperReducer
    }
}

export default connect(
    mapProps,
    {closeRightPaper}
)(RightPaperHeader)