import React from 'react';
import "./SegmentCard.css";
import "./Card.css";

export default function SegmentCard({data, onClick}) {

    return (
        <div onClick={(ev)=>{onClick(data.segmentId)}} className="Card">
            <span className={"Avatar" + (data.enabled ? " SegmentAvatarEnabled" : " SegmentAvatarDisabled")}>S</span>
            <span className="scope">{data.scope}</span>
            <span className="name">{data.name}</span>
        </div>
    );
}